import React from 'react'
import logo from '../../assets/logo.svg';
import { Layout, Typography, Input, Menu, Button, Dropdown } from 'antd'
import { GlobalOutlined } from '@ant-design/icons';
import styles from './Header.module.css'
import { withRouter, RouteComponentProps } from '../../helpers/withRouter'
import store from '../../redux/store'
import { languageState } from '../../redux/languageReducer'
// languageStateState
interface State extends languageState {}

class HeaderComponent extends React.Component<RouteComponentProps, State>{

  // constructorstore
  constructor(props) {
    super(props)
    // store
    const storeState = store.getState()
    this.state = {
      language: storeState.language,
      languageList: storeState.languageList
    }
    // store store store 
    store.subscribe(this.handleStoreChange)
  }

  handleStoreChange = () => {
    const storeState = store.getState()
    // setState  state  store
    this.setState({
      language: storeState.language,
      languageList: storeState.languageList
    })
  }

  menuClickHandler = (e) => {
    console.log(e)
    if (e.key === "new") {
      // action
      const action = {
        type: "add_language",
        payload: { code: "new_lang", name: "" }
      }
      store.dispatch(action)
    } else {
      const action = {
        type: "change_language",
        payload: e.key
      }
      store.dispatch(action)
    }
  }

  render () {
    const { navigate } = this.props

    // const language = [
    //   { key: '1', label: ''},
    //   { key: '2', label: 'English'}
    // ]

    const menu = [
      { key: "1", label: "" },
      { key: "2", label: "" },
      { key: "3", label: "" },
      { key: "4", label: "" },
      { key: "5", label: "" },
      { key: "6", label: "" },
      { key: "7", label: "+" },
      { key: "8", label: "" },
      { key: "9", label: "" },
      { key: "10", label: "" },
      { key: "11", label: "" },
      { key: "12", label: "" },
      { key: "13", label: "" },
      { key: "14", label: "" },
      { key: "15", label: "" },
      { key: "16", label: "" }
    ]

    return(
      <div className={styles['app-header']}>
        {/* top-header */}
        <div className={styles['top-header']}>
          <div className={styles.inner}>
            {/* <Typography.Text></Typography.Text> */}
            <Typography.Text className={styles['header-title-slogan']}></Typography.Text>
            <Dropdown.Button
              style={{ marginLeft: 15, width: '90%' }}
              overlay={
                <Menu onClick={this.menuClickHandler}
                  items={[
                    ...this.state.languageList.map(item => {
                      return { key: item.code, label: item.name}
                    }),
                    { key: "new", label: "" }
                  ]}
                />
              }
              icon={<GlobalOutlined />}
            >
              { this.state.language === "zh" ? "" : "English" }
            </Dropdown.Button>
          </div>
          <Button.Group className={styles['button-group']}>
            <Button onClick={() => navigate('/register')}></Button>
            <Button onClick={() => navigate('/signin')}></Button>
          </Button.Group>
        </div>
        <Layout.Header className={styles['main-header']}>
          <img src={logo} alt="" className={styles['App-logo']} />
          <Typography.Title level={3} className={styles.title}>React </Typography.Title>
          <Input.Search className={styles['search-input']} placeholder={', , '}></Input.Search>
        </Layout.Header>
        <Menu mode={'horizontal'} className={styles['main-menu']}
          items={menu}
        />
      </div>
    )
  }
}

export const Header = withRouter(HeaderComponent)
